<template>
  <div class="tool">
    <el-tooltip
      v-for="(item, index) in setting"
      effect="dark"
      :content="item.content"
      placement="right"
    >
      <i
        :class="[{ active: index === active }, item.class]"
        v-if="index < 1"
        @click="changeTool(item.event, index)"
      ></i>
      <i
        v-else
        :class="[{ active: index === active }, item.class]"
        @click="changeEvent(item.event, index)"
      ></i>
    </el-tooltip>

    <!-- <div class="status">
      {{this.currentStatus}}
    </div> -->

    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="configDialogVisiable"
      width="400px"
      :show-close="false"
      append-to-body
    >
      <el-form
        label-position="left"
        label-width="100px"
        size="mini"
        :model="config"
      >
        <el-form-item label="">
          <el-color-picker
            v-model="config.path_strokeStyle"
            size="mini"
          ></el-color-picker>
        </el-form-item>
        <el-form-item label="">
          <el-color-picker
            v-model="config.point_strokeStyle"
            size="mini"
          ></el-color-picker>
        </el-form-item>
        <el-form-item label="">
          <el-input-number
            size="mini"
            v-model="config.path_lineWidth"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="">
          <el-input-number
            size="mini"
            v-model="config.point_lineWidth"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="">
          <el-input-number
            size="mini"
            v-model="config.point_radis"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="">
          <el-switch
            v-model="config.grid"
            active-color="#66a6ff"
            inactive-color="#999"
          ></el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-switch
            v-model="config.guid"
            active-color="#66a6ff"
            inactive-color="#999"
          ></el-switch>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="configDialogVisiable = false"
          > </el-button
        >
        <el-button
          size="small"
          type="primary"
          @click="configDialogVisiable = false"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
//  props  data  render
export default {
  name: "topbar",
  props: {
    currentStatus: String,
    path_strokeStyle: String,
    selected: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      active: Infinity,
      setting: [
        { class: "el-icon-news", event: "rectangle", content: "" },
        { class: "el-icon-zoom-in", event: "zoomIn", content: "" },
        {
          class: "el-icon-zoom-out",
          event: "zoomOut",
          content: "",
        },
        {
          class: "el-icon-s-grid",
          event: "zoomInit",
          content: "",
        },
        {
          class: "el-icon-rank",
          event: "move",
          content: "",
        },
        {
          class: "el-icon-full-screen",
          event: "fullScreen",
          content: "",
        },
        {
          class: "el-icon-delete",
          event: "clearAll",
          content: "",
        },
        {
          class: "el-icon-setting",
          event: "setting",
          content: "",
        },
      ],
      color1: "#409EFF",
      configDialogVisiable: false,
      contrast: 50, // 
      brightness: 50, // 
      // 
      config: {
        path_lineWidth: 1,
        path_strokeStyle: this.path_strokeStyle,
        point_lineWidth: 1,
        point_strokeStyle: "#fff",
        point_radis: 5,
        grid: false,
        guid: false,
      },
    };
  },
  watch: {
    config: {
      handler() {
        this.$emit("configChange", this.config);
      },
      immediate: true,
      deep: true,
    },
    contrast: {
      handler() {
        this.$emit("contrastChange", Math.floor(this.contrast - 50));
      },
      immediate: true,
    },
    brightness: {
      handler() {
        this.$emit("brightnessChange", Math.floor(this.brightness - 50));
      },
      immediate: true,
    },
    selected: {
      handler() {
        if (this.selected) {
          this.$nextTick(() => {
            this.changeTool("rectangle", 0);
          });
        }
      },
      immediate: true,
    },
    // selected() {
    //   debugger;
    //   if (this.selected) {
    //     this.$nextTick(() => {
    //       this.changeTool("rectangle", 0);
    //     });
    //   }
    // },
  },
  methods: {
    changeTool(eventName, index) {
      this.active = index;
      this.$emit("toolSelected", eventName);
    },
    changeEvent(eventName, index) {
      this.active = index;
      if (eventName === "setting") {
        this.configDialogVisiable = true;
        return;
      }
      this.$emit("topBarEvent", eventName);
    },
    formatTooltip(val) {
      return parseInt(val - 50);
    },
  },
};
</script>

<style lang="scss" scoped>
.tool {
  width: 30px;
  margin: 0px 5px;
  .active {
    background: #000;
  }
}
[class*=" el-icon-"],
[class^="el-icon-"] {
  box-sizing: border-box;
  height: 30px;
  width: 30px;
  line-height: 30px;
  font-size: 20px;
  padding: 0 5px;
  border-bottom: 1px dashed rgba(0, 0, 0, 0.2);
  cursor: pointer;
  &:nth-child(1) {
    margin-top: 5px;
  }
  &:hover {
    border-radius: 3px;
    background-color: rgba(0, 0, 0, 0.4);
  }
}
</style>
